body {
  margin: 0;
  font-family: var(--font-main);
  color: var(--color-text);
  background: var(--body-bg-image)
              var(--body-bg-repeat)
              var(--body-bg-position)
              var(--body-bg-attachment);
  background-size: var(--body-bg-size);
  background-position: var(--body-bg-final-position);
}
